import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import newCost from '@/views/modules/newCost'

Vue.use(Router)

export default new Router({
  build: {
    assetsPublicPath: './'
  },
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/newCost',
      name: 'newCost',
      component: newCost
    }
  ]

})



